
.fadeIn-enter-active {
  transition: all .4s ease;
}
.fadeIn-leave-active {
  transition: all 0.2s cubic-bezier(1, 0.5, 0.8, 1);
}
.fadeIn-enter, .fadeIn-leave-active {
  -webkit-transform: translateX(-250px);
          transform: translateX(-250px);
  opacity: 0;
}
.aside-menu {
  transition: .3s ease;
  /*transform: translate(-60%);*/
  width: 100%;
  height: 100%;
  position: absolute;
  left: -100%;
  bottom: 0;
  right: 0;
  top: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  z-index: 10;
}
.aside-menu.show {
    left: 0;
}
.aside-menu .aside {
    height: 100%;
    -webkit-box-flex: 6;
        -ms-flex: 6;
            flex: 6;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    min-width: 192px;
    background: red;
}
.aside-menu .aside .info {
      padding: 20px 0 10px 10px;
      background: #B72712;
      box-shadow: 2px 0 5px darkgray;
      text-align: center;
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
}
.aside-menu .aside .info img {
        width: 100px;
        height: 95px;
        border-radius: 50%;
}
.aside-menu .aside .setting {
      position: relative;
      -webkit-box-flex: 5;
          -ms-flex: 5;
              flex: 5;
      /*height: 100%;*/
      background: #eee;
      box-shadow: 2px 0 5px darkgray;
}
.aside-menu .aside .setting .content ul {
        list-style: none;
        padding: .5rem 1.5rem;
}
.aside-menu .aside .setting .content ul li {
          font-size: 1rem;
          height: 2rem;
}
.aside-menu .mask {
    -webkit-box-flex: 4;
        -ms-flex: 4;
            flex: 4;
    height: 100%;
    background: rgba(0, 0, 0, 0.3);
}

